import { observer } from 'mobx-react-lite';
import React, { useContext } from 'react'
import { Col, Container, Row } from 'react-bootstrap';
import { useNavigate } from 'react-router-dom';
import { Context } from '..';

import { CATEGORY_ROUTE, CONTACT_ROUTE, DELIVERY_ROUTE, PAYMENT_ROUTE, REVIEWS_ROUTE } from '../utils/consts';
const Footer = observer(() => {

    const {product} = useContext(Context)
    const navigate = useNavigate()

    return (




<Container fluid className="mt-5 footer">
    <Container  >
    <div className="card  ">
        <div className="row mb-4 mt-5">
            <div className="col-md-4 col-sm-11 col-xs-11">
                <div className="footer-text pull-left">
                    <div className="d-flex">
                        AlcoTown
                    </div>
                    <p className="card-text">         </p>
                   
                </div>
            </div>
            <div className="col-md-2 col-sm-1 col-xs-1 mb-2"></div>
            <div className="col-md-2 col-sm-1 col-xs-1 mb-2"></div>
            <div className="col-md-2 col-sm-4 col-xs-4">
                <h5 className="heading"></h5>
                <ul>

                {product.types.map(type => {    
                   return( <li key={type.id} onClick={()=> {  navigate(CATEGORY_ROUTE + '' + type.id)}}  >{type.name}</li> )   
            })}
                  
                </ul>
            </div>
            <div className="col-md-2 col-sm-4 col-xs-4">
                <h5 className="heading"> </h5>
                <ul className="card-text">
                <li onClick={() => navigate(CATEGORY_ROUTE) + '/1'} ></li>
                    <li onClick={() => navigate(PAYMENT_ROUTE)} ></li>
                    <li onClick={() => navigate(DELIVERY_ROUTE)}></li>
                    <li onClick={() => navigate(CONTACT_ROUTE)}></li>
                </ul>
            </div>

        </div>
        <div className="divider mb-4"> </div>
        <div className="row" style={{fontSize:'10px'}}>
            <div className="col-md-6 col-sm-6 col-xs-6">
                <div className="pull-left">
                    <p><i className="fa fa-copyright"></i> 2022 </p>
                </div>
            </div>
            <div className="col-md-6 col-sm-6 col-xs-6">
                <div className="pull-right mr-4 d-flex policy">
                    

                </div>
            </div>
        </div>
    </div>
</Container>

</Container>




    );

})

export default Footer;