import { useContext } from "react"
import { Button, Col, Container, Nav, Navbar } from "react-bootstrap"
import { Context } from ".."

import {  CATEGORY_ROUTE, CONTACT_ROUTE, PAYMENT_ROUTE, REVIEWS_ROUTE, DELIVERY_ROUTE } from "../utils/consts"
import { observer } from "mobx-react-lite"
import { useNavigate } from "react-router-dom"
import { declOfNum } from "../utils/functions"


 const NavBar = observer(() => {
    const { cart} = useContext(Context)
    const navigate = useNavigate()
    return (
        <>
        <Navbar bg="white" variant="light">
            <Container>
            <Navbar.Brand onClick={() => navigate(CATEGORY_ROUTE + '1')} style={{cursor: 'pointer'}}>
                lkoTown
            </Navbar.Brand>

            <Navbar.Collapse id="responsive-navbar-nav">
                
                <Nav className="me-auto d-none d-md-flex">
                    <Nav.Link onClick={() => navigate('/1')  } ></Nav.Link>
                    <Nav.Link onClick={() => navigate(PAYMENT_ROUTE)} ></Nav.Link>
                    <Nav.Link onClick={() => navigate(DELIVERY_ROUTE)}></Nav.Link>
                    <Nav.Link onClick={() => navigate(CONTACT_ROUTE)}></Nav.Link>
                </Nav>
                <div style={{marginRight: '1rem'}}><a style={{textDecoration: 'none', fontWeight: '500', color: '#333'}} href='tel:89829015064'>8 (982) 901-50-64</a></div>

            </Navbar.Collapse>
            </Container>
        </Navbar>

        </>
    )
})

export default NavBar
