import React, {useContext} from 'react';
import { observer } from "mobx-react-lite"
import { Context } from '..';
import { useParams, useNavigate } from 'react-router-dom';
import { CATEGORY_ROUTE } from '../utils/consts';
import { Button, Card, Col, Image, Row } from 'react-bootstrap';
import cartImg from '../assets/img/basket.png'
import cartFullImg from '../assets/img/basket-full.png'

const CategoryItem = observer(() => {
    const {product, cart} = useContext(Context)
    const param = useParams()
    const navigate = useNavigate ()

    console.log(param.slug)
    /*<Button key={type.id} onClick={   ()=> {  navigate(CATEGORY_ROUTE + '' + type.id)}   } variant={params.slug == type.id ? "pink" : "outline pink"}>{type.name}</Button>*/
    return(


        <Row>

            {product.products.map(p => {  
                if(p.category == param.slug)  
                   return( 
                   

                    <Col md={4} key={p.id + p.name} className={"mt-3"} >
                        <Card className='br7' style={{cursor: 'pointer'}} border={"light"}>
                            <Image className="brt7" style={{width: '100%', height: 'auto'}}  src={ p.img} />
                            <div className="text-black-50 m-3 d-flex flex-column justify-content-between align-items-center">
                                <div  className=" d-flex flex-column">
                                    <div >{p.name} </div>
                                    <div >{p.ob} </div>
                                    
                                </div>
                                <div className="d-flex flex-row mt-2 align-items-center justify-content-around w-100" >
                                    <div className='text-black' ><b>{p.price} </b></div>
                                    <a class="btn btn-primary btn-lg text-white" href="tel:+79829015064" role="button"></a>
                                       
                                </div>
                            </div>
                        </Card>
                    </Col>

                   
                   )   
            })}
           
                
        </Row>
 
    )
})
export default CategoryItem