import { observer } from 'mobx-react-lite';
import React, { useContext } from 'react'
import { Col, Container, Image, Row } from 'react-bootstrap';
import { useNavigate, useParams } from 'react-router-dom';
import { Context } from '..';


const Product = observer(() => {
    const {product} = useContext(Context)
    const params = useParams()
    const t = product.getType( params.slug)
    const d = product.getProduct( params.productid)
    const navigate = useNavigate()

    
    if(d === null || t === null)
        return <>...</>

    console.log(d.name)
    return (

        
        <Container className='mt-4'>
            <Row>
            <nav aria-label="breadcrumb">
                <ol className="breadcrumb">
                    <li className="breadcrumb-item" onClick={( )  => navigate('/' + t.id)} style={{cursor: 'pointer',}}><p>{t.name}</p></li>
                    <li className="breadcrumb-item active" aria-current="page">{d.name}</li>
                </ol>
            </nav>
                
            </Row>
            <Row>
                <Col md={6}><Image width='100%' src={ d.img} /></Col>
                <Col md={6}>
                    <h3>{d.name}</h3>


                    <h5><b>: {d.price}  </b></h5>
                    <p>{d.desc}</p>
                </Col>
                
            </Row>
            
        </Container>

    );



})

export default Product;