import React from 'react'

import Category from "./pages/Category";
import Contact from './pages/Contact';
import Delivery from './pages/Delivery';
import Home from './pages/Home';
import Payment from './pages/Payment';
import Product from "./pages/Product";
import Reviews from './pages/Reviews';
import { PRODUCT_ROUTE, CATEGORY_ROUTE, DELIVERY_ROUTE, PAYMENT_ROUTE, REVIEWS_ROUTE, CONTACT_ROUTE } from "./utils/consts";

export const publicRoutes = [
    {
        path: '/',
        Component: <Home/>
    },
    {
        path: DELIVERY_ROUTE,
        Component: <Delivery/>
    },
    {
        path: PAYMENT_ROUTE,
        Component: <Payment/>
    },
    {
        path: REVIEWS_ROUTE,
        Component: <Reviews/>
    },
    {
        path: CONTACT_ROUTE,
        Component: <Contact/>
    },
    {
        path: CATEGORY_ROUTE + ':slug',
        Component: <Category/>
    },
    {
        path: PRODUCT_ROUTE ,
        Component: <Product/>
    }
]