import React, {useContext} from 'react';
import { observer } from "mobx-react-lite"
import { Button } from "react-bootstrap"
import { Context } from '..';
import { useParams, useNavigate } from 'react-router-dom';
import { CATEGORY_ROUTE } from '../utils/consts';


const TypeButton = observer(() => {
    const {product} = useContext(Context)
    const params = useParams();
    const navigate = useNavigate ()

    
    return(
        <div style={{overflowX: 'auto', width: '100%', display: 'flex'}}>
            {product.types.map(type => {    
                   return( <Button key={type.id} onClick={   ()=> {  navigate(CATEGORY_ROUTE + '' + type.id)}   } style={{whiteSpace: 'nowrap' }} variant={params.slug == type.id ? "pink" : "outline pink"}>{type.name}</Button>)   
            })}
 
        </div>
    )
})
export default TypeButton